<template>
        <div class="container-fluid">
          <div class="row justify-content-center">
  <b-card
    style="max-width: 40rem;"
    class="mb-2"
  >
    <b-card-title>Apply Leave Form</b-card-title>
    <b-card-body>
      <b-form @submit="onSubmit">
        <b-container class="bv-example-row">
          <b-row>
