            </b-col>
          </b-row>
          <b-row>
            <b-col style="max-width:30rem">
              <b-form-group
                id="input-group-1"
                >
                <b-form-input
                  id="input-1"
                  type="text"
                  v-model="demo.name"
                  required
                  placeholder="Name"
                  >
                </b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
              <b-col style="max-width:15rem">
                <label>Start Date</label>
                <b-row>
            <b-col style="max-width:30rem">
              <b-form-group
                id="input-group-1"
                >
                <b-form-input
                  id="input-1"
                  type="date"
                  v-model="demo.sdate"
                  required
                  placeholder="Project Name"
                  >
                </b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
