<script>

import firebase from 'firebase'
import router from '../router'

export default {
  name: 'AddBoard',
  data () {
    return {
      ref: firebase.firestore().collection('applyLeave'),
      demo: {}
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()

      this.ref.add(this.demo).then((docRef) => {
        this.demo.eid = ''
        this.demo.name = ''
        this.demo.sdate = ''
        this.demo.edate = ''
        router.push({
          name: 'ApplyLeave'
        })
      })
        .catch((error) => {
          alert('Error adding document: ', error)
        })
    }
  }
}
</script>

