<script>

import firebase from 'firebase'
import router from '../router'

export default {
  name: 'AddBoard',
  data () {
    return {
      ref: firebase.firestore().collection('emsDemo'),
      demo: {}
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()

      this.ref.add(this.demo).then((docRef) => {
        this.demo.fname = ''
        this.demo.email = ''
        this.demo.date = ''
        this.demo.number = ''
        this.demo.address = ''
        this.demo.degree = ''
        this.demo.amt = ''
        this.demo.eid = ''
        this.demo.deprtment = ''
        router.push({
          name: 'AddEmployee'
        })
      })
        .catch((error) => {
          alert('Error adding document: ', error)
        })
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.demo.email, this.demo.eid)
        .then(data => {
          data.user
            .updateProfile({
              displayName: this.demo.fname
            })
            .then(() => {})
        })
        .catch(err => {
          this.error = err.message
        })
    }
  }
}
