  <b-card
    style="max-width: 40rem;"
    class="mb-2"
  >
    <b-card-title>Assign Project</b-card-title>
    <b-card-body>
      <b-form @submit="onSubmit">
        <b-container class="bv-example-row">
          <b-row>
            <b-col style="max-width:30rem">
              <b-form-group
                id="input-group-1"
                >
                <b-form-input
                  id="input-1"
                  type="text"
                  v-model="demo1.eid"
                  required
                  placeholder="Employee ID"
                  >
                </b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col style="max-width:30rem">
              <b-form-group
                id="input-group-1"
                >
                <b-form-input
                  id="input-1"
                  type="text"
                  v-model="demo1.pname"
                  required
                  placeholder="Project Name"
                  >
                </b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
            <b-row>
              <b-col style="max-width:2rem">
                <label>Date</label>
              </b-col>
            </b-row>
            <b-row>
            <b-col style="max-width:30rem">
              <b-form-group
                id="input-group-1"
                >
                <b-form-input
                  id="input-1"
                  type="date"
                  v-model="demo1.ddate"
                  required
                  placeholder="Project Name"
                  >
                </b-form-input>
              </b-form-group>
