<script>

import firebase from 'firebase'
import router from '../router'

export default {
  name: 'AssignProject',
  data () {
    return {
      ref: firebase.firestore().collection('assProject'),
      demo1: {}
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()

      this.ref.add(this.demo1).then((docRef) => {
        this.demo1.pname = ''
        this.demo1.ddate = ''
        this.demo1.eid = ''
        router.push({
          name: 'AssignProject'
        })
      })
        .catch((error) => {
          alert('Error adding document: ', error)
        })
    }
  }
}
</script>

