<template>
<b-container fluid>
  <b-row>
    <b-col cols="12">
      <b-table striped hover :items="emsDemo" :fields="fields">
        <template slot="actions" scope="row">
          <b-btn size="sm" @click.stop="details(row.item)">Details</b-btn>
        </template>
      </b-table>
    </b-col>
  </b-row>
  </b-container>
</template>

<script>

import firebase from 'firebase'
import router from '../router'

export default {
  name: 'BoardList',
  data () {
    return {
      fields: {
        employeeId: { label: 'employeeId', sortable: true, class: 'text-left' },
        name: { label: 'name', sortable: true, class: 'text-left' },
        email: { label: 'email', sortable: true, class: 'text-left' },
        birthdate: { label: 'birthdate', sortable: true, class: 'text-left' },
        address: { label: 'address', sortable: true, class: 'text-left' },
        degree: { label: 'degree', sortable: true, class: 'text-left' },
        number: { label: 'number', sortable: true, class: 'text-left' },
        actions: { label: 'Action', class: 'text-center' }
      },
      emsDemo: [],
      errors: [],
      ref: firebase.firestore().collection('emsDemo')
    }
  },
  created () {
    this.ref.onSnapshot((querySnapshot) => {
      this.emsDemo = []
      querySnapshot.forEach((doc) => {
        this.emsDemo.push({
          // key: doc.id,
          employeeId: doc.data().eid,
          name: doc.data().fname,
          email: doc.data().email,
          birthdate: doc.data().date,
          address: doc.data().address,
          degree: doc.data().degree,
          number: doc.data().number
        })
      })
    })
  },
  methods: {
    details (board) {
      router.push({ name: 'ShowBoard', params: { id: board.key } })
    }
  }
}
</script>

<style>
  .table {
    background-image: url(../assets/ems7.jpg);
    width: 96%;
    margin: 0 auto;
  }
</style>
