import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import AdminLogin from '../views/AdminLogin.vue'
import EmployeeLogin from '../components/EmployeeLogin.vue'
import adminLogin112 from '../components/adminLogin.vue'
import AddEmployee from '../components/AddEmployee.vue'
import ViewEmployee from '../components/ViewEmployee.vue'
import AssignProject from '../components/AssignProject.vue'
import SalaryTable from '../components/SalaryTable.vue'
import ProjectStatus from '../components/ProjectStatus.vue'
import EmployeeLeave from '../components/EmployeeLeave.vue'
import MyProfile from '../components/MyProfile.vue'
import MyProject from '../components/MyProject.vue'
import ApplyLeave from '../components/ApplyLeave.vue'
import EmployeeDashboard from '../components/EmployeeDashboard.vue'
import Login from '../components/Login'
import Register from '../components/Register'
import Dashboard from '../components/Dashboard'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/AdminLogin',
    name: 'AdminLogin',
    component: AdminLogin
  },
  {
    path: '/EmployeeLogin',
    name: 'EmployeeLogin',
    component: EmployeeLogin
  },
  {
    path: '/adminLogin112',
    name: 'adminLogin112',
    component: adminLogin112
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/AddEmployee',
    name: 'AddEmployee',
    component: AddEmployee
  },
  {
    path: '/ViewEmployee',
    name: 'ViewEmployee',
    component: ViewEmployee
  },
  {
    path: '/AssignProject',
    name: 'AssignProject',
    component: AssignProject
  },
  {
    path: '/SalaryTable',
    name: 'SalaryTable',
    component: SalaryTable
  },
  {
    path: '/ProjectStatus',
    name: 'ProjectStatus',
    component: ProjectStatus
  },
  {
    path: '/MyProfile',
    name: 'MyProfile',
    component: MyProfile
  },
  {
    path: '/MyProject',
    name: 'MyProject',
    component: MyProject
  },
  {
    path: '/ApplyLeave',
    name: 'ApplyLeave',
    component: ApplyLeave
  },
  {
    path: '/EmployeeDashboard',
    name: 'EmployeeDashboard',
    component: EmployeeDashboard
  },
  {
    path: '/EmployeeLeave',
    name: 'EmployeeLeave',
    component: EmployeeLeave
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
